<template>
  <el-container>
    <el-header>
      <div style="text-align: left; float: left; line-height: 60px; font-style: italic; font-weight: 400; font-size: 25px; letter-spacing: 0.478em;">
        <span>校友录系统</span>
      </div>
      <div style="text-align: right; line-height: 60px; float: right; font-size: 14px;">
        <el-link href="login">登录</el-link>
        <span>-</span>
        <el-link href="regist">注册</el-link>
      </div>
    </el-header>
    <el-main>
      <div style="width: 1309px; margin: auto;">
        <el-tabs type="border-card" v-model="activeName" @tab-click="handleClick" :stretch="true">
        <el-tab-pane name="first" label="首页">
          <el-carousel :interval="4000" type="card" height="200px">
            <el-carousel-item style="height: 200px; width: 645px" v-for="url in urls" :key="url">
              <el-image :src="url" :fit="'cover'"></el-image>
            </el-carousel-item>
          </el-carousel>
          <div style="width: 1309px;">
            <el-breadcrumb separator="/">
              <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
              <el-breadcrumb-item>推荐新闻</el-breadcrumb-item>
            </el-breadcrumb>
            <div style="float: left;">
              <el-row :gutter="10">
              <el-col :span="1.8">
                <el-card :body-style="{padding:'0px'}" class="box-mcard">
                  <span style="text-align: center; display: block; padding-top: 8px;">2020-01</span>
                  <span style="text-align: center; display: block; font-size: 18px;">06</span>
                </el-card>
              </el-col>
              <el-col :span="7"><el-card class="box-bcard"></el-card></el-col>
              </el-row>
            </div>
            <div style="float: left; margin-left: 30px;">
              <el-row :gutter="10">
              <el-col :span="1.8">
                <el-card :body-style="{padding:'0px'}" class="box-mcard">
                  <span style="text-align: center; display: block; padding-top: 8px;">2020-01</span>
                  <span style="text-align: center; display: block; font-size: 18px;">06</span>
                </el-card>
              </el-col>
              <el-col :span="7"><el-card class="box-bcard"></el-card></el-col>
              </el-row>
            </div>
            <div style="float: left; margin-left: 30px;">
              <el-row :gutter="10">
              <el-col :span="1.8">
                <el-card :body-style="{padding:'0px'}" class="box-mcard">
                  <span style="text-align: center; display: block; padding-top: 8px;">2020-01</span>
                  <span style="text-align: center; display: block; font-size: 18px;">06</span>
                </el-card>
              </el-col>
              <el-col :span="7"><el-card class="box-bcard"></el-card></el-col>
              </el-row>
            </div>
          </div>
          <div style="float: left; width: 1275px;"><el-divider></el-divider></div>
          <el-card class="box-scard" style="margin-left:10px; width: 1260px">
            <div slot="header" class="clearfix">
              <span>校友留言</span>
              <el-table
                :data="tableData"
                stripe
                style="width: 1260px">
                <el-table-column
                  prop="timestamp"
                  label="日期"
                  width="680">
                </el-table-column>
                <el-table-column
                  prop="content"
                  label="内容"
                  width="580">
                </el-table-column>
              </el-table>
            </div>
          </el-card>
        </el-tab-pane>
        <el-tab-pane name="second" label="新闻中心">
          
        </el-tab-pane>
        <el-tab-pane name="third" label="校友留言">
          <el-table
            :data="tableData"
            stripe
            style="width: 100%">
            <el-table-column
              prop="timestamp"
              label="日期"
              width="180">
            </el-table-column>
            <el-table-column
              prop="content"
              label="内容"
              width="680">
            </el-table-column>
            <el-table-column
              prop="author"
              label="留言人">
            </el-table-column>
          </el-table>
          <el-button type="text" @click="dialogFormVisible = true" style="margin-top: 10px" round>
            添加留言
          </el-button>
        </el-tab-pane>
        <el-tab-pane name="fourth" label="校友日志">
          <div>
            <el-table
              :data="logList"
              style="width: 100%">
              <el-table-column
                label="日期"
                width="180">
                <template slot-scope="scope">
                  <i class="el-icon-time"></i>
                  <span style="margin-left: 10px">{{ scope.row.date }}</span>
                </template>
              </el-table-column>
              <el-table-column
                label="标题"
                width="500">
                <template slot-scope="scope">
                  <el-popover trigger="hover" placement="top">
                    <p>标题: {{ scope.row.title }}</p>
                    <p>内容: {{ scope.row.content }}</p>
                    <p>发布人: {{ scope.row.author }}</p>
                    <div slot="reference" class="name-wrapper">
                      <el-tag size="medium">{{ scope.row.title }}</el-tag>
                    </div>
                  </el-popover>
                </template>
              </el-table-column>
              <el-table-column prop="author" label="发布人">
              </el-table-column>
            </el-table>
            <el-button type="text" @click="dialogFormVisible = true" style="margin-top: 10px" round>
            写日志
          </el-button>
          </div>
        </el-tab-pane>
        </el-tabs>
      </div>
    </el-main>
    <el-dialog title="添加留言" :visible.sync="dialogFormVisible">
      <el-form @submit.native.prevent="upMsg" ref="form" :model="form">
        <el-form-item label="留言内容" :label-width="formLabelWidth">
          <el-input v-model="form.content" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="留言人" :label-width="formLabelWidth">
          <el-input v-model="form.author" :disabled="true" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" native-type="submit">确 定</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
        <el-dialog title="写日志" :visible.sync="dialogFormVisible">
      <el-form @submit.native.prevent="upLog" ref="form" :model="form_logs">
        <el-form-item label="日志标题" :label-width="formLabelWidth">
          <el-input v-model="form_logs.title" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="日志内容" :label-width="formLabelWidth">
          <el-input v-model="form_logs.content" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="发表人" :label-width="formLabelWidth">
          <el-input v-model="form_logs.author" :disabled="true" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" native-type="submit">确 定</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </el-container>
</template>

<style>
  .el-header {
    background-color: #B3C0D1;
    color: #333;
    /* text-align: center; */
    height: 10vh;
  }

  .el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
    height: 90vh;
  }

  /* body > .el-container {
    margin-bottom: 40px;
  } */
  body >.el-container {
    height: 100vh;
  }

  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 200px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }

  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }

  .el-row {
    margin-top: 20px;
  }
  .el-col {
    border-radius: 4px;
  }

  .box-mcard {
    width: 50px;
    height: 50px;
    font-size: 13px;
  }
  .box-bcard {
    width: 341px;
    height: 50px;
  }

  .box-scard {
    font-size: 14px;
    width: 416px;
    text-align: left;
    float: left;
  }
  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }

</style>

<script>
export default {
  data () {
    return {
      urls: [
        require('../../static/image/1.jpg'),
        require('../../static/image/2.jpg'),
        require('../../static/image/3.jpg'),
        require('../../static/image/4.jpg'),
        require('../../static/image/5.jpg'),
        require('../../static/image/6.jpg')
      ],
      activeName: 'first',
      dialogFormVisible: false,
      form: {
        content: '',
        author: localStorage.getItem('Info')
      },
      form_logs: {
        title: '',
        content: '',
        author: localStorage.getItem('Info')
      },
      formLabelWidth: '120px',
      tableData:[],
      logList:[]
    }
  },
  methods: {
    handleClick (tab, event) {
      // console.log(this.activeName)
    },
    upMsg() {
      this.$http.post('messages', this.form).then(res => {
        if (res.status == 1){
          console.log("留言成功!")
          this.fetch()
        }
      })
    },
    upLog() {
      this.$http.post('logs', this.form_logs).then(res => {
        if (res.status == 1 ){
          console.log("日志写入成功!")
          this.fetch_logs()
        }
      })
    },
    fetch() {
      this.$http.get('messages').then( res => {
        this.tableData = res.data
        // console.log(res.data[0].src)
      })
    },
    fetch_logs() {
      this.$http.get('logs').then(res => {
        this.logList = res.data
      })
    }
  },
  created() {
    this.fetch()
    this.fetch_logs()
  }
}
</script>
